<template>
  <div id="encyclopedias">


    <new-layout>
    <template slot="top">
      <announce-header>指标详情</announce-header>
    </template>


      <template slot="center">

        <header>{{res.result.dn}}</header>

        <table>
          <tr>
            <td class="width100">疾病名称:</td>
            <td>{{res.result.dn}}</td>
          </tr>
          <tr>
            <td class="width100">所属科室</td>
            <td>{{res.result.ad}}</td>
          </tr>
          <tr>
            <td class="width100">疾病概述</td>
            <td>{{res.result.dm}}</td>
          </tr>
          <tr>
            <td class="width100">疾病分类</td>
            <td>{{res.result.dc}}</td>
          </tr>
        </table>
        <div class="disease_detail">
          疾病详情
        </div>
        <van-collapse v-model="activeNames">
          <van-collapse-item title="病因" name="1">
            <li v-for="(item,index) in dd_cArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>
          <van-collapse-item title="症状" name="2">

            <li v-for="(item,index) in dd_sArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>

          <van-collapse-item title="风险或并发症" name="3">
            <li v-for="(item,index) in dd_rArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>






          <van-collapse-item title="专家建议" name="4">



            <van-collapse-item title="生活方式" name="5">
              <li v-for="(item,index) in dd_lArray" class="sugges_li">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
            </van-collapse-item>





            <van-collapse-item title="饮食建议" name="6">
              <span class="suit">宜</span>
              <li v-for="(item,index) in dd_dArray" class="sugges_li">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
              <span class="avoid">忌</span>
              <li v-for="(item,index) in dd_dArray1" class="sugges_li">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
            </van-collapse-item>





          <!--  <van-collapse-item title="饮食建议忌" name="7">
              <li v-for="(item,index) in dd_dArray1" class="sugges_li">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
            </van-collapse-item>-->


            <van-collapse-item title="医疗保健" name="8">
              <li v-for="(item,index) in dd_mArray" class="sugges_li">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
            </van-collapse-item>





          </van-collapse-item>













          <van-collapse-item title="运动建议" name="9">
            <li v-for="(item,index) in dd_saArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>





          <van-collapse-item title="生活常识" name="10">
            <li v-for="(item,index) in dd_nArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>

          <van-collapse-item title="就医复查指南" name="11">

            <li v-for="(item,index) in dd_gArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>
        </van-collapse>




      </template>
    </new-layout>







  </div>
</template>

<script>


  export default {
    data(){
      return {
        activeNames:['0'],
        res:'',
        dd_lArray:[],
        dd_dArray:[],
        dd_saArray:[],
        dd_mArray:[],
        dd_nArray:[],
        dd_cArray:[],//病因
        dd_sArray:[],
        dd_rArray:[],
        dd_gArray:[],
        dd_dArray1:[],
      }
    },

    methods:{
      async init(){
        let _this=this;
        let key=this.$route.query.code;
        this.res= await this.$http.get(this.$HOST+'/open/xk/symptom?code='+key);

        this.common(this.res.result.dd_l,_this.dd_lArray)


        this.common(this.res.result.dd_d_s,_this.dd_dArray)
        this.common(this.res.result.dd_d_a,_this.dd_dArray1)

        console.log(this.res.result.dd_d_s);
        console.log(this.res.result.dd_d_a);



        this.common(this.res.result.dd_sa,_this.dd_saArray)
        this.common(this.res.result.dd_m,_this.dd_mArray)
        this.common(this.res.result.dd_n,_this.dd_nArray)
        this.common(this.res.result.dd_c,_this.dd_cArray)
        this.common(this.res.result.dd_s,_this.dd_sArray)
        this.common(this.res.result.dd_r,_this.dd_rArray)
        this.common(this.res.result.dd_g,_this.dd_gArray)
    },
   common(data,arrayname) {
       let resLen=data.split("@#");
        for(var i=0; i<resLen.length; i++){
          if(resLen[i]){
            arrayname.push(resLen[i].split("#@"));
          }
        }
      }
  },
    created(){
      this.init();
    }
  }
</script>

<style scoped lang="scss">
  $height80: 80px;
  $bg-fff: #ffffff;
  #encyclopedias {
    background: #f3f3f3;
    .sugges_li {
      border-bottom: 1px dashed #999999;
      margin-bottom: 12px;
      padding: 10px;
    }
    .title {
      color: orange;
      font-size: 24px;
    }
    p:nth-child(2){
      font-size: 18px;
    }
  }

  table{
    width: 96%;
    margin: auto;
    tr{
      height: 50px;
      line-height: 50px;
      td{
        font-size: 18px;
      }
      td:nth-child(2){
        font-weight: bold;
        color: #333333;
      }
    }
  }
  .van-cell__title span{
    font-size: 18px!important;
  }
  .width100{
    width: 100px;
  }

  header{
    font-size: 30px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }
  .disease_detail{
     color: #089ec3;
    font-size: 15px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }

  .suit{
    width: 30px;
    height: 30px;
    background: #68ba8a;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }
  .avoid{
    width: 30px;
    height: 30px;
    background: #d95d5d;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }
</style>



